import React from 'react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { AnimatePresence } from 'framer-motion';

// Components
import Landing from './components/Landing';
import Navbar from './components/Navbar';
import Home from './components/Home';
import About from './components/About';
import MyGardens from './components/MyGardens';
import GardenDetail from './components/GardenDetail';
import PlantLibrary from './components/PlantLibrary';
import Weather from './components/Weather';
import GardenJournal from './components/GardenJournal';
import Design from './components/Design';
import Help from './components/Help';
import Contact from './components/Contact';

const AppContainer = styled.div`
  min-height: 100vh;
  display: flex;
  flex-direction: column;
`;

const MainContent = styled.main`
  flex: 1;
  padding: 20px;
  background-color: #f5f5f5;
`;

// Separate component for routes to access location
const AnimatedRoutes = () => {
  const location = useLocation();
  const [isLanding, setIsLanding] = React.useState(true);

  // Update isLanding based on current path
  React.useEffect(() => {
    setIsLanding(location.pathname === '/');
  }, [location.pathname]);

  return (
    <>
      {!isLanding && <Navbar />}
      <MainContent>
        <AnimatePresence mode="wait">
          <Routes location={location} key={location.pathname}>
            <Route 
              path="/" 
              element={<Landing onEnter={() => setIsLanding(false)} />} 
            />
            <Route path="/home" element={<Home />} />
            <Route path="/about" element={<About />} />
            <Route path="/my-gardens" element={<MyGardens />} />
            <Route path="/garden/:id" element={<GardenDetail />} />
            <Route path="/plant-library" element={<PlantLibrary />} />
            <Route path="/weather" element={<Weather />} />
            <Route path="/journal" element={<GardenJournal />} />
            <Route path="/design" element={<Design />} />
            <Route path="/help" element={<Help />} />
            <Route path="/contact" element={<Contact />} />
          </Routes>
        </AnimatePresence>
      </MainContent>
    </>
  );
};

const App: React.FC = () => {
  return (
    <Router>
      <AppContainer>
        <AnimatedRoutes />
      </AppContainer>
    </Router>
  );
};

export default App;
